import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { CategorieEntite } from '@app/shared/entites/nomenclature/CategorieEntite';
import { Jurid } from '@app/shared/entites/nomenclature/Jurid';
import { ConfigService } from '@app/shared/services/config.service';
import { LayoutService } from '@app/shared/services/layout.service';
import { CategorieEntiteService } from '@app/shared/services/nomenclature/categorie-entite.service';
import { EntitesService } from '@app/shared/services/nomenclature/entites.service';
import { JuridService } from '@app/shared/services/nomenclature/jurid.service';
import { AppTranslateService } from '@app/shared/translate/app-translate.service';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-add-edit-entite',
    templateUrl: './add-edit-entite.component.html',
    styleUrls: ['./add-edit-entite.component.scss']
})
export class AddEditEntiteComponent implements OnInit {
    formEntite: FormGroup;
    listeEntites: any;
    listeCategorieEntites: any;
    listeJuird: Jurid[];
    selectedValueJuird: Jurid;
    selectedValueCategorieEntite: CategorieEntite;
    submit = true;
    codeExist = false;
    config: any;
    currentLang: any;
    dir: any;

    constructor(private formBuilder: FormBuilder,
        public entiteService: EntitesService,
        private router: Router,
        private toastr: ToastrService,
        public juridService: JuridService,
        public categorieEntiteService: CategorieEntiteService,
        @Inject(MAT_DIALOG_DATA) public data,
        private dialogRef: MatDialogRef<AddEditEntiteComponent>,
        private layoutService: LayoutService,
        public translate: TranslateService,
        private configService: ConfigService
    ) {

        this.translate.onDefaultLangChange.subscribe((language) => {
            console.log(this.translate.getDefaultLang() + ' vs ' + language.lang);
            this.currentLang = language.lang;
            this.dir = this.currentLang === 'ar' ? 'rtl' : 'ltr';
        });

    }

    ngOnInit() {


        this.config = this.configService.templateConf;
        this.dir = this.config.layout.dir;
        this.currentLang = this.dir === 'rtl' ? 'ar' : 'fr';

        this.getListJuird();
        this.getListCategorieEntite();

        this.getListEntites();
        this.submit = true;
        this.selectedValueJuird = new Jurid(this.data.id, this.data.idNmjurid, this.data.codeJurid, this.data.libelleFrJurid, this.data.libelleArJurid);
        this.selectedValueCategorieEntite = new CategorieEntite(this.data.id, this.data.idNmCategEntite, this.data.codeCategEntite, this.data.libelleArCategEnt, this.data.libelleFrCategEnt);
        console.log(this.data, this.selectedValueJuird)
        console.log('test', this.selectedValueCategorieEntite);

        this.formEntite = this.formBuilder.group({
            code: [this.data.code, Validators.required],
            libelleAr: [this.data.libelleAr, Validators.required],
            libelleFr: [this.data.libelleFr, Validators.required],
            ordre: [this.data.ordre, Validators.required],
            idNmJurid: [this.data.idNmjurid, Validators.required],
            idNmCategorieEntite: [this.data.idNmCategEntite, Validators.required]

        })
    }

    public getListEntites() {

        this.entiteService.getListEntite().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeEntites = response['payload'];

                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )

    }

    getListCategorieEntite() {
        debugger;
        this.categorieEntiteService.getListCategorieEntite().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeCategorieEntites = response['payload'];

                    console.log('listeeeentiteCategorie', this.listeCategorieEntites);

                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )
    }

    getListJuird() {
        debugger;
        this.juridService.getlistJurdiction().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeJuird = response['payload'];
                    console.log('listeeeentite', this.listeJuird);
                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )
    }

    onCancel() {
        this.router.navigate(['/nomenclature/entites']);
    }


    isCodeExist(value) {

        this.codeExist = false;
        this.codeExist = this.listeEntites.find(ident => ident.code == value)

        this.codeExist = this.codeExist ? true : false;
        return this.codeExist;

    }

    addEntite() {


        let dataTosend;
        if (this.formEntite.valid) {
            this.submit = true;
            this.isCodeExist(this.formEntite.get('code').value);
            if (this.codeExist === false) {
                // this.submit= true ;
                this.entiteService.addEntite(this.formEntite.value).subscribe(
                    response => {
                        if (response['code'] === '200') {
                            dataTosend = this.data;
                            this.entiteService.affmodif === false;
                            this.submit = true;
                            this.entiteService.selectEntite(dataTosend);
                            this.dialogRef.close();
                            this.submit = true;
                            this.entiteService.selectEntite(dataTosend);


                        } else if (response['code'] === '411') {
                            this.toastr.error('ce code entité existe dans le systeme', '');
                        } else {
                            console.log('-1:' + response['code']);
                            this.toastr.error('Erreur intern du systéme', '');
                        }

                    },
                    error => {
                        console.log('-2');
                        this.toastr.error('Erreur intern du systéme', '');
                    });
            }
        } else {

            this.submit = false;
        }


    }


    editEntite(value) {
        debugger;
        let dataTosend;
        if (this.formEntite.valid) {

            this.submit = true;
            value.id = this.data.id
            this.entiteService.updateEntite(value).subscribe(
                response => {
                    this.entiteService.affmodif === true;
                    this.dialogRef.close();
                    this.entiteService.selectEntite(dataTosend);



                });
        }
        else

            this.submit = false;
    }
    onNoClick(): void {
        this.dialogRef.close();
    }


}
