import {Component, OnInit} from '@angular/core';
import {FormBuilder, FormGroup} from '@angular/forms';
import {MatDialog} from '@angular/material';
import {MatTableDataSource} from '@angular/material/table';
import {CONSTANTE_SQL, CriteriaSearch, SearchObject} from '@app/shared';
import {CategorieEntite} from '@app/shared/entites/nomenclature/CategorieEntite';
import {Jurid} from '@app/shared/entites/nomenclature/Jurid';
import {VEntite} from '@app/shared/entites/nomenclature/VEntite';
import {ConfigService} from '@app/shared/services/config.service';
import {LayoutService} from '@app/shared/services/layout.service';
import {CategorieEntiteService} from '@app/shared/services/nomenclature/categorie-entite.service';
import {EntitesService} from '@app/shared/services/nomenclature/entites.service';
import {JuridService} from '@app/shared/services/nomenclature/jurid.service';
import {SearchObjectTreatment} from '@app/shared/tools';
import {AddEditEntiteComponent} from './add-edit-entite/add-edit-entite.component';
import {DeleteEntiteComponent} from './delete-entite/delete-entite.component';

@Component({
    selector: 'app-entite',
    templateUrl: './entite.component.html',
    styleUrls: ['./entite.component.scss']
})
export class EntiteComponent implements OnInit {

    listeEntites: VEntite[];
    dataSource: any;
    rows = [];
    countPage: Number;
    currentLang: any;
    selectedRow: any;
    temp = [];
    isPopupOpened = true;

    displayedColumns: any[] = ['code', 'libelle', 'libelleJurid', 'libelleCategEnt', 'action'];
    // displayedColumns: any[] = ['code'] ;

    selectedEntites: VEntite;

    searchObject: SearchObject;
    currentlistEntite: VEntite;
    entiteById: any;
    formEntiteSearch: FormGroup;
    listeJuird: any;
    selectedValueJuird: Jurid;
    selectedValueCategorieEntite: CategorieEntite;
    listeCategorieEntites: any;
    limit = 10;
    offSet: Number;


    toggleSearchCardFlag: boolean;
    currentDir: string;
    public config: any = {};

    constructor(public entiteService: EntitesService,
                private searchObjectTreatment: SearchObjectTreatment,
                public categorieEntiteService: CategorieEntiteService,
                private dialog: MatDialog,
                private formBuilder: FormBuilder,
                public juridService: JuridService,
                private layoutService: LayoutService,
                private configService: ConfigService) {

        layoutService.changeEmitted$.subscribe(
            direction => {
                this.currentLang = direction === 'rtl' ? 'ar' : 'fr';
                console.log(this.currentLang);
            });
    }

    ngOnInit() {

        this.selectedValueJuird = new Jurid;
        this.selectedValueCategorieEntite = new CategorieEntite;
        this.toggleSearchCardFlag = false;

        this.searchObject = this.searchObjectTreatment.treatementSearchObject(10, 0, null, null);

        this.initDataList();

        this.entiteService.selectEntiteSubject.subscribe((data) => {

            this.dataSource = null;

            this.getListEntites();
        })

        this.getListEntites();
        this.getListJuridiction();
        this.getListCategorieEntite();

        this.config = this.configService.templateConf;
        this.currentLang = this.config.layout.dir === 'rtl' ? 'ar' : 'fr';


        this.formEntiteSearch = this.formBuilder.group({
            code: [null],
            libelle: [null],
            idNmJurid: [this.selectedValueJuird.id],
            idNmCategorieEntite: [this.selectedValueCategorieEntite.id]

        })

    }

    search() {
  
        this.searchObject.pagination.offSet = 0;
        this.searchObject.pagination.limit = 10;
        this.searchObject.dataSearch = [];


        if (this.formEntiteSearch.get('code').value !== undefined && this.formEntiteSearch.get('code').value !== '' && this.formEntiteSearch.get('code').value !== null) {
            this.searchObject.dataSearch.push(new CriteriaSearch('code', (this.formEntiteSearch.get('code').value).toString(),
                CONSTANTE_SQL.EQUALS
                )
            );
        }

        if (this.formEntiteSearch.get('libelle').value !== undefined && this.formEntiteSearch.get('libelle').value !== '' && this.formEntiteSearch.get('libelle').value !== null && this.currentLang === 'ar') {
            this.searchObject.dataSearch.push(new CriteriaSearch('libelleAr', (this.formEntiteSearch.get('libelle').value).toString(),
                CONSTANTE_SQL.UPPER_LIKE
                )
            );
        }

        if (this.formEntiteSearch.get('libelle').value !== undefined && this.formEntiteSearch.get('libelle').value !== '' && this.formEntiteSearch.get('libelle').value !== null && this.currentLang === 'fr') {
            this.searchObject.dataSearch.push(new CriteriaSearch('libelleFr', (this.formEntiteSearch.get('libelle').value).toString(),
                CONSTANTE_SQL.UPPER_LIKE
                )
            );
        }

        if ((this.formEntiteSearch.get('idNmJurid').value) && this.formEntiteSearch.get('idNmJurid').value !== '' && (this.formEntiteSearch.get('idNmJurid').value) !== null) {
            this.searchObject.dataSearch.push(new CriteriaSearch('idNmjurid', (this.formEntiteSearch.get('idNmJurid').value).toString(),
                CONSTANTE_SQL.EQUALS
                )
            );
        }
        if ((this.formEntiteSearch.get('idNmCategorieEntite').value) !== undefined && this.formEntiteSearch.get('idNmCategorieEntite').value !== '' && this.formEntiteSearch.get('idNmCategorieEntite').value !== null) {
            this.searchObject.dataSearch.push(new CriteriaSearch('idNmCategEntite', (this.formEntiteSearch.get('idNmCategorieEntite').value).toString(),
                CONSTANTE_SQL.EQUALS
                )
            );
        }

        this.initDataList();


    }

    toggleSearchCard() {
        this.toggleSearchCardFlag = !this.toggleSearchCardFlag;
    }

    public getListEntites() {

        this.entiteService.getListEntite().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeEntites = response['payload'];
                    this.dataSource = new MatTableDataSource<VEntite>(this.listeEntites);
                    console.log('aaaaaaaaaaaaa', this.listeEntites);


                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )

    }


    getListCategorieEntite() {

        this.categorieEntiteService.getListCategorieEntite().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeCategorieEntites = response['payload'];

                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )
    }


    getListJuridiction() {


        this.juridService.getlistJurdiction().subscribe(
            response => {
                if (response['code'] === '200') {
                    this.listeJuird = response['payload'];
                    console.log('listeeeentite', this.listeJuird);
                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )

    }

    public initDataList() {

    
        this.entiteService.ListEntitesWithSearch(this.searchObject).subscribe(
            response => {
                if (response['code'] === '200') {
                    this.countPage = response['payload']['total'];
                    this.rows = response['payload']['data'];

                    this.countPage = response['payload']['total'];

                    this.dataSource = new MatTableDataSource<VEntite>(
                        response['payload']['data']
                    );
                    console.log(this.dataSource);
                    this.temp = this.rows;
                }
            },
            error => {
                console.log('error :: ' + error);
            }
        )
    }

    public getRow(entite: VEntite) {

        debugger;


        this.selectedEntites = null;
        this.selectedEntites = entite;
        this.selectedEntites.selected = true;


        if (this.selectedEntites.id != null) {
            this.getEntiteById(this.selectedEntites.id);

        }


        // this.removeSelection();


    }

    getEntiteById(id) {
        debugger;
        this.entiteService.getEntiteById(id).subscribe(response => {
            if (response['code'] === '200') {
                this.entiteById = response['payload'];
            }
        }, err => {
            console.log(err);
        })
    }


    addEntite() {
        debugger;
        this.isPopupOpened = true;

        const dialogRef = this.dialog.open(AddEditEntiteComponent, {


            width: '50%',

            data: {}
        });


        console.log('farahajout', dialogRef);


        dialogRef.afterClosed().subscribe(result => {
            this.isPopupOpened = false;
        });


    }


    editEntite() {
        debugger;
        let entiteModif;

        entiteModif = {};

        this.isPopupOpened = true;

        this.currentlistEntite = {...this.selectedEntites};
        entiteModif = this.currentlistEntite;
        const dialogRef = this.dialog.open(AddEditEntiteComponent, {


            width: '50%',
            // height:'70%',
            data:
            entiteModif


        });


        dialogRef.afterClosed().subscribe(result => {
            this.isPopupOpened = false;
        });


    }


    private removeSelection() {
        this.dataSource.map((entite: VEntite) => {
            return entite.selected = false;

        });


    }

    openConfirmDialog() {

        debugger;
        return this.dialog.open(DeleteEntiteComponent, {

            width: '390px',
            panelClass: 'confirm-dialog-container',

            data: {
                message: ' Êtes-vous sûr de vouloir supprimer cet entite ?'
            }

        });
    }


    deleteEntite() {


        this.openConfirmDialog().afterClosed().subscribe(
            response => {
                if (response) {

                    this.entiteService.deleteEntite(this.selectedEntites.id).subscribe(data => {

                        if (data['code'] === '200') {
                            this.initDataList();
                            this.listeEntites = null;

                            // this.toaster.success('Supprimé avec succés') ;
                        }
                    });

                }


            });

    }


}
